export default {
    data: function () {
        return {
            su:true,
            fu:false,
            sus:true,
            fus:false,
            yes: [],
            tz: [],
            sf: '-1',
            sfs:'-1',
            sfss:'',
            chuan: 1,
            sfc1: [],
            show: -1,
            list: [],
            index: 0,
            bs: [],
            multiple: '',
            game_id: '',
            cate_id: '',
            tz_result: [],
            len: null,
            i1:'false',
            i2:'false',
            chuan:[],
            cont:[],
            conts:[],
            shows:false,
            sh:true,
            ii:'',
            arrs: [],
            bigList:[],
            bigLists:[],
            con:[],
            bei:1,
            tzz:{},
            money:'',
            bigListss:[],
            qing:true,
            qings:false,
            showss:true
        }
    }
    ,
    mounted() {
        this.isloginList();
        this.footballList();
        this.tokenList();
    },
    methods: {
        liangClick(i,c){
            if( $('#bt li').eq(i).hasClass("liang")){
                this.arrs.forEach((e,i)=>{
                    if(JSON.stringify(e)==JSON.stringify(c)){
                       this.arrs.splice(i,1);   
                    }
                }) 
                $('#bt li').eq(i).removeClass('liang');
                $('#bt li').eq(i).children('p').removeClass('white');

            }else{
               
                this.arrs.push(c);
                $('#bt li').eq(i).addClass('liang');
                $('#bt li').eq(i).children('p').addClass('white');

            }
            var co = JSON.stringify(this.cont); 
            var ch=this.arrs.toString();
           
            if(this.arrs.length==0){
                this.sus=true;
                this.fus=false;
                this.su=true;
                this.fu=false;
                
            }else{
                this.sus=false;
                this.fus=true;
                this.su=false;
                this.fu=true;
            }
             var obj={
                multiple:this.bei,
                chuan:ch,
                tz:co
             }
             var qs = require('querystring');
            this.http.post("/football/football/get_tz/",qs.stringify(obj))
            .then(res=>{
                if(res.data.code==1){
                    this.tzz=res.data.msg;
                    this.money=res.data.msg.order_money;
                    layer.msg(res.data.msg,{time:3500});

                }else{                   
                    // layer.msg(res.data.msg,{time:3500});
                  

                }
            })
            if(this.cont.length==0){
                this.fus=false;
                this.sus=true;
                this.fu=false;
                this.su=true;
            }
        },
        showClick(){
           
            this.shows=true;
            this.con=this.cont.slice(0);
            for(var i = 0; i < this.con.length-1; i++){
                for(var j = i+1; j < this.con.length; j++){
                    if(this.con[i].game_id==this.con[j].game_id){

                    this.con.splice(j,1);
                    j--;

                    }
                }
            }
            if(this.con.length==0){
                this.shows=false;
            }else if(this.con.length==1){
                this.chuan=[1];
            }else if(this.con.length==2){
                this.chuan=[1,2];
            }else if(this.con.length==3){
                this.chuan=[1,2,3];
            }else if(this.con.length==4){
                this.chuan=[1,2,3,4];
            }else if(this.con.length==5){
                this.chuan=[1,2,3,4,5];
            }else if(this.con.length==6){
                this.chuan=[1,2,3,4,5,6];
            }else if(this.con.length==7){
                this.chuan=[1,2,3,4,5,6,7];
            }else if(this.con.length==8){
                this.chuan=[1,2,3,4,5,6,7,8];
            }
        },
        isloginList() {
            this.http.post("/home/login/is_login/")
                .then(res => {
                    if (res.data.code == 0) {
                        window.location.href = "#/";
                    } else if (res.data.code == 1) {
                        window.location.href = "#/football";
                    }
                })
        },
        tokenList() {
            this.http.post("/football/football/token/")
                .then(res => {
                    if (res.data.code == 0) {

                    } else if (res.data.code == 1) {
                        this.token = res.data.token;
                    }
                })
        },
        footballList() {
            this.http.post("/football/football/index")
            .then(res => {
                if (res.data.code == 1) {
                    this.showss=false;
                    this.list = res.data.msg;
                    res.data.msg.forEach((a,b)=>{
                        var arr = [];
                        a.forEach((c,d)=>{
                            var ar=[];
                            arr.push(ar);
                        })
                        this.bigList.push(arr);
                        
                    })
                } else {
                    this.showss=false;
                    layer.msg(res.data.msg, { time: 1500 });
                }
            })
        },
        sfcClick(i,j,k) {
            //
            this.sh=false;
            this.i1 = i;
            this.i2=j;
            this.shows=false;
            $('.session').eq(i).find('.ss').eq(j).find('.dd').removeClass("none");
        },
        xClick(){
            this.i1 = 'false';
        },

        rwClick(i,j,k,id,cate_id) {
            this.shows=false;
            this.fu=true;
            this.su=false;
            this.sus=false;
            this.fus=true;
            var obj={
                game_id:id,
                tz_result:cate_id
            }
            if( $('.session').eq(i).find('.ss').eq(j).find('.rw').eq(k).hasClass("red")){
                this.cont.forEach((e,i)=>{
                    if(JSON.stringify(e)==JSON.stringify(obj)){
                       this.cont.splice(i,1);   
                    }
                }) 
                $('.session').eq(i).find('.ss').eq(j).find('.rw').eq(k).removeClass('red');
                $('.session').eq(i).find('.ss').eq(j).find('.rw').eq(k).children('.ssr11').removeClass('white');
                $('.session').eq(i).find('.ss').eq(j).find('.rw').eq(k).children('.ssr12').removeClass('white');

            }else{
               
                this.cont.push(obj);
                $('.session').eq(i).find('.ss').eq(j).find('.rw').eq(k).addClass('red');
                $('.session').eq(i).find('.ss').eq(j).find('.rw').eq(k).children('.ssr11').addClass('white');
                $('.session').eq(i).find('.ss').eq(j).find('.rw').eq(k).children('.ssr12').addClass('white');
            }
          
            var co = JSON.stringify(this.cont); 
            var ch=this.arrs.toString();
            if(this.arrs.length==0){
                ch=1;
            }else{
                ch=this.arrs.toString();
            }
             var obj={
                multiple:this.bei,
                chuan:ch,
                tz:co
             }
             var qs = require('querystring');
            this.http.post("/football/football/get_tz/",qs.stringify(obj))
            .then(res=>{
                if(res.data.code==1){
                    this.tzz=res.data.msg;
                    this.money=res.data.msg.order_money;
                    layer.msg(res.data.msg,{time:3500});

                }else{                   
                    // layer.msg(res.data.msg,{time:3500});
                  

                }
            })
            if(this.cont.length==0){
                this.fus=false;
                this.sus=true;
                this.fu=false;
                this.su=true;
            }
        },


        clearClick() {
            this.shows=false;
            this.fus=false;
            this.sus=true;
            this.su=true;
            this.fu=false;
            $('.session').find('.ss').find('.rw').removeClass('red');
            $('.session').find('.ss').find('.rw').children('.ssr11').removeClass('white');
            $('.session').find('.ss').find('.rw').children('.ssr12').removeClass('white');
            $('.session').find('.ss').find('.tw').removeClass('red');
            $('.session').find('.ss').find('.tw').removeClass('white');
            $('.session').find('.ss').find('.tw').children('.p2').removeClass('white');

            $('.session').find('.ss').find('.tw1').removeClass('red');
            $('.session').find('.ss').find('.tw1').children('p').removeClass('white');
            $('.session').find('.ss').find('.tw1').children('.pp3').removeClass('white');
            $('.session').find('.ss').find('.tw2').removeClass('red');
            $('.session').find('.ss').find('.tw2').children('p').removeClass('white');
            $('.session').find('.ss').find('.tw2').children('.pp3').removeClass('white');
            $('.session').find('.ss').find('.tw3').removeClass('red');
            $('.session').find('.ss').find('.tw3').children('p').removeClass('white');
            $('.session').find('.ss').find('.tw3').children('.pp3').removeClass('white');
            $('#bt li').removeClass('liang');
            $('#bt li').children('p').removeClass('white');
            this.cont=[];
            this.bigListss=[];
            this.list.forEach((a,b)=>{
                var arr = [];
                a.forEach((c,d)=>{
                    var ar=[];
                    arr.push(ar);
                   
                })
                this.bigListss.push(arr);
            })
            this.bigList=this.bigListss.slice(0);
        },
        sureClick() {
            if((this.cont.length!=0)&&(this.chuan.length!=0)){
                this.su=false;
                this.fu=true;
                this.fus=true;
                this.sus=false;
                this.qing=false;
                this.qings=true;

            }
           
            var co = JSON.stringify(this.cont); 
            var ch=this.arrs.toString();
             var obj={
                multiple:this.bei,
                chuan:ch,
                tz:co
             }
             var qs = require('querystring');
            this.http.post("/football/football/get_tz/",qs.stringify(obj))
            .then(res=>{
                if(res.data.code==1){
                    this.tzz=res.data.msg;
                    this.money=res.data.msg.order_money;
                    layer.msg(res.data.msg,{time:3500});

                }else{                   
                    layer.msg(res.data.msg,{time:3500});
                  

                }
            })
        },
        fuClick(){
            this.su=true;
            this.sus=true;
            this.fu=false,
            this.fus=false;
            this.shows=false;
            this.qing=true;
            this.qings=false;
            var co = JSON.stringify(this.cont); 
            var ch=this.arrs.toString();
            var obj={
                multiple:this.bei,
                chuan:ch,
                token:this.token,
                tz:co,
                order_money:this.money
             }
             var qs = require('querystring');
            this.http.post("/football/football/add_order/",qs.stringify(obj))
            .then(res=>{
                if(res.data.code==1){
                    layer.msg(res.data.msg,{time:3500});
                    this.tokenList();  
                    this.cont=[];
                    this.bei=1;
                    $('.session').find('.ss').find('.rw').removeClass('red');
                    $('.session').find('.ss').find('.rw').children('.ssr11').removeClass('white');
                    $('.session').find('.ss').find('.rw').children('.ssr12').removeClass('white');
                    $('.session').find('.ss').find('.tw').removeClass('red');
                    $('.session').find('.ss').find('.tw').removeClass('white');
                    $('.session').find('.ss').find('.tw').children('.p2').removeClass('white');

                     $('.session').find('.ss').find('.tw1').removeClass('red');
                    $('.session').find('.ss').find('.tw1').children('p').removeClass('white');
                    $('.session').find('.ss').find('.tw1').children('.pp3').removeClass('white');
                    $('.session').find('.ss').find('.tw2').removeClass('red');
                    $('.session').find('.ss').find('.tw2').children('p').removeClass('white');
                    $('.session').find('.ss').find('.tw2').children('.pp3').removeClass('white');
                    $('.session').find('.ss').find('.tw3').removeClass('red');
                    $('.session').find('.ss').find('.tw3').children('p').removeClass('white');
                    $('.session').find('.ss').find('.tw3').children('.pp3').removeClass('white');
                    $('.session').find('.ss').find('.dd').addClass("none");
                    $('#bt li').removeClass('liang');
                    $('#bt li').children('p').removeClass('white');
                    this.bigListss=[];
                    this.list.forEach((a,b)=>{
                        var arr = [];
                        a.forEach((c,d)=>{
                            var ar=[];
                            arr.push(ar);
                        })
                        this.bigListss.push(arr);
                    })
                    this.bigList=this.bigListss.slice(0);
                    this.con=null;
                    this.arrs=[];
                }else{
                    this.arrs=[];
                    layer.msg(res.data.msg,{time:3500});
                    this.tokenList();
                    this.bei=1;
                    $('.session').find('.ss').find('.rw').removeClass('red');
                    $('.session').find('.ss').find('.rw').children('.ssr11').removeClass('white');
                    $('.session').find('.ss').find('.rw').children('.ssr12').removeClass('white');
                    $('.session').find('.ss').find('.tw').removeClass('red');
                    $('.session').find('.ss').find('.tw').removeClass('white');
                    $('.session').find('.ss').find('.tw').children('.p2').removeClass('white');
                    $('.session').find('.ss').find('.dd').addClass("none");
                    $('#bt li').removeClass('liang');
                    $('#bt li').children('p').removeClass('white');
                    this.bigListss=[];
                    this.list.forEach((a,b)=>{
                        var arr = [];
                        a.forEach((c,d)=>{
                            var ar=[];
                            arr.push(ar);
                        })
                        this.bigListss.push(arr);
                    })
                    this.bigList=this.bigListss.slice(0);
                    this.cont=[];
                    this.con=null;


                }
            })
        },
