                tz:co
             }
             var qs = require('querystring');
            this.http.post("/football/football/get_tz/",qs.stringify(obj))
            .then(res=>{
                if(res.data.code==1){
                    this.tzz=res.data.msg;
                    this.money=res.data.msg.order_money;
                    layer.msg(res.data.msg,{time:3500});

                }else{                   
                    // layer.msg(res.data.msg,{time:3500});
                  

                }
            })
            if(this.cont.length==0){
                this.fus=false;
                this.sus=true;
                this.fu=false;
                this.su=true;
            }
        },
        backClick() {
            window.location.href = '#/choose';
        },
    }, 
  


}






