import {mapState} from 'vuex';
import Login from '../components/Login.vue'

export default {
    data:function(){
        return{
            order:[],
            login:''
        }
    },
    methods:{
        render(){
            this.http.post("/lol/lol/order/")
           .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.order=res.data.msg;
                }
             
           })
           
        },
        isloginList(){
                  this.http.post("/home/login/is_login/")
                  .then(res=>{
                  
                  if(res.data.code==0){
                          window.location.href="#/";
                      }else if(res.data.code==1){
                          window.location.href="#/comorder";
                      }
              })
          },
          backClick(){
              //history.back();
              window.location.href="#/commine";

          },
           
    },

  mounted:function(){
      this.render();
      this.isloginList();
  },
  components:{
      Login
  },
  filters:{
      sz(s){
          if(s=='1'){
              return s='1';
          }else if(s=='2'){
              return s='2';
          }
      }
      
  }
  
}



