import QRCode from 'qrcode'
import {mapState} from 'vuex';
import Login from '../components/Login.vue'

    export default {
        data:function(){
            return{
                showdraw:false,
                showcharge:false,
                tixian:null,
                chongzhi:'',
                money:[],
                login:'',
                showcode:false,
                zhifu:[],
                codes:'',
                shuju:[100,300,500,1000,3000,5000],
              
                index:'',
                shu:100,
                 sh:false
            }
        },
        methods:{
          
            withdrawClick(){
               
                this.http.post('/home/user/is_tixian/')
                .then(res=>{
                    if(res.data.code==0){
                        window.location.href="#/dialattest";
                        layer.msg("",{time:1500});
                    }else{
                        this.showdraw=true;

                    }
                })
            },
             rechargeClick(){
                                this.sh=true;

                this.showcharge=true;
            },
             t2Click(){
                this.sh=false;
            },
            cancelClick(){
                this.showdraw=false;
                this.showcharge=false;
            },
            cancelClick1(){
                this.showcode=false;
               
            },
            tixianClick(){
                  
                    var mbArr={
                        money:this.tixian, 
                    }
                    var qs = require('querystring');
                    this.http.post('/home/user/tixian/',qs.stringify(mbArr))
                    .then(res=>{
                        if(res.data.code==0){
                            layer.msg(res.data.msg,{time:1500});
                        }else if(res.data.code==1){
                            layer.msg("",{time:1500});
                            window.location.href="#/dialaccount"
                            this.showdraw=false;
                            
                        }else if(res.data.code==2){
                            layer.msg(res.data.msg,{time:1500});
                        }else if(res.data.code==3){
                            layer.msg(res.data.msg,{time:1500});
                        }else  if(res.data.code==4){
                            layer.msg(res.data.msg,{time:1500});
                        }
                    })
            },
            exitClick(){
                this.http.post('/home/login/logout/')
                .then(res=>{
                    if(res.data.code==0){
                        layer.msg(res.data.msg,{time:1500});
                    }else if(res.data.code==1){
                        layer.msg(res.data.msg,{time:1500});
                        window.location.href="#/"
                      
                    }
                })
            },
            renzhengClick(){
                this.http.post('/home/user/get_renzheng/')
                .then(res=>{
                    if(res.data.code==0){
                        window.location.href="#/dialattest"
                    }else if(res.data.code==1){
                        window.location.href="#/dialattestrevise"
                      
                    }
                })
            },
            comClick(){
              if($("#mon").val()<$("#tixian").val()){
                  $("#sure").attr("disabled",true);
              }else{
                $("#sure").attr("disabled",false);

              }
            },
            allClick(){
                this.tixian=this.money.yh.balance;
       
            },
            moneyList(){
                this.http.post("/home/user/purse/")
                .then(res=>{
                        if(res.data.code==0){

                        }else if(res.data.code==1){
                            this.money=res.data.msg;
                        
                        }
                    
                })
            },
            zhifuBlur(){
                if(this.chongzhi%10!=0){
                    layer.tips("10","#chongzhi",{tips:[1,"black"]});
                    
                }
            },
            isloginList(){
                    this.http.post("/home/login/is_login/")
                    .then(res=>{
                    
                    if(res.data.code==0){
                            // this.login=0;
                            window.location.href="#/";
                        }else if(res.data.code==1){
                            // this.login=1;
                            window.location.href="#/dialmine";
                        }
                 
                })
            },
            zhifuClick(){
                this.showcode=true;
                this.showcharge=false;
                var zhifuArr={
                    price:this.shu,
                    istype:1,
                    return_url:'http://www.202252.com:443/#/dialmine'
                }
                var qs = require('querystring');
                this.http.post("/home/pay/pay/",qs.stringify(zhifuArr))
                .then(res=>{
                
                if(res.data.code==0){     

                }else if(res.data.code==1){   
                     $("#pay_amount").val(res.data.data.pay_amount);
                    $("#pay_applydate").val(res.data.data.pay_applydate);
                    $('#pay_attach').val(res.data.data.pay_attach);
                    $('#pay_bankcode').val(res.data.data.pay_bankcode);
                    $('#pay_callbackurl').val(res.data.data.pay_callbackurl);
                    $('#pay_md5sign').val(res.data.data.pay_md5sign);
                    $('#pay_memberid').val(res.data.data.pay_memberid);
                    $('#pay_notifyurl').val(res.data.data.pay_notifyurl);
                    $('#pay_orderid').val(res.data.data.pay_orderid);
                    $('#pay_productname').val(res.data.data.pay_productname);
                    $('#user_name').val(res.data.data.user_name);
                    $('#submitdemo1').click();         
                }
                
                })
    
      

            },
            getCode(){

                var canvas=document.getElementById('canvas');
                
                QRCode.toCanvas(canvas,this.zhifu.qrcode,function(error){
                   
                })
            },
            backClick(){
                //history.back();
                window.location.href="#/dial";
            },
            liClick(i){
               
                $("#u li").eq(i).toggleClass('red');
                this.index=i;
                this.shu=$("#u li").eq(i).text()*1;
              
              
            }


        },

        mounted:function(){
            this.moneyList();
            this.isloginList();
            
        
        },
        filters:{
            fi(s){
                
                s=s.substring(0,s.length-3);
                return s;

            },
            cod(s){
                s=s.substring(0,6);
                return s;
            }
        },
        components:{
            Login
        },
        computed:{
            isYe(){
                return  this.money.yh.amount_money*1>=this.tixian;
            },
        

        }
          
   }



