import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
import axios from "axios";

var store = new Vuex.Store({
   state:{
        attest:[],
        mb:[],
        tmb:[],

   },
   mutations:{
       setAttest:function(state,value){
           state.attest=value;
       },
       setMb:function(state,value){
           state.mb=value;
       },

       setTmb:function(state,value){
           state.tmb=value;
       },
   },
   actions:{
       attestList:function(state,data){
           axios.post("/home/user/get_renzheng/")
           .then(res=>{
                state.commit("setAttest",res.data.msg);
            });
       },
       mbList:function(state,data){
           axios.post("/home/login/get_mb/")
           .then(res=>{
               state.commit("setMb",res.data.msg);
           })
       },
       tmbList:function(state,data){
           axios.post('/home/login/forget_getmb/')
           .then(res=>{
               state.commit("setTmb",res.data.msg);
           })
       },     
   }
});


export default store;


