export default {
   data:function(){
       return{
            i1:false,
            i2:false,
            i3:true,
            list:[],
            m:[],
            n:[],
            id:'',
            odds:'',
            result:'',
            money:'',
            arr:[],
            count:null,
            arrs:[],
            cids:'',
            results:'',
            balance:[],
            show:false
       }
   },
   mounted(){
        this.isloginList();
        this.comList();
        this.tokenList();
        this.countdown();
        this.balanceList();
   },
   methods:{
        isloginList(){
            this.http.post("/home/login/is_login/")
            .then(res=>{  
               if(res.data.code==0){
                    window.location.href="#/";
                }else if(res.data.code==1){
                    window.location.href="#/com";
                }
           })
        },
        balanceList(){
            this.http.post("/dice/game/balance/")
            .then(res=>{
                if(res.data.code==1){
                    this.balance=res.data.data;
                }
            })
        },
        backClick(){
            window.location.href='#/choose';
        },
        comClick(i){
         
            this.i1=i;
            this.i2=!this.i2;
            this.i3=!this.i3;
        },
        comList(){
            this.http.post("/lol/lol/index")
            .then(res=>{  
                if(res.data.code==1){
                    this.list=res.data.msg;
                }else{
                    layer.msg(res.data.msg,{time:1500});
                }
           })
        },
        tokenList(){
            this.http.post("/lol/lol/token/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.token=res.data.token;
                }
            })
        },
        touClik(a,b,h,k){
            var val = $(".hh").eq(h).find('.ss').eq(k).children('.si').val();
            this.id=a;
            this.odds=b;
            var obj={
                cid:this.id,
                tz_odds:this.odds,
                tz_money:val
            }
            if(this.arr.length==0){
                this.arr.push(obj);
            }else{
                var a=true;
                this.arr.forEach((e,i)=>{
                    if(e.cid==obj.cid){
                        a=false;
                        e.tz_money=obj.tz_money;
                    }else{
                        a=true;
                    }
                    if(e.tz_money==""){
                        this.cids=e.cid;
                        this.results=e.tz_result;
                        this.arr.forEach((e,i)=>{
                            if(e.cid==this.cids){
                                this.arr.splice(i,1); 
                            }
                        })
                    }
                })
                if(a==true){
                    this.arr.push(obj);
                }
            }
        },
        touzClik(a,b,c,h,i,j){ 
            if(c=='1'){
                var val = $(".hh").eq(h).find('.u').eq(i).find('.d3').eq(j).children('.dd').val();
            }else if(c=='2'){
                var val = $(".hh").eq(h).find('.u').eq(i).find('.d4').eq(j).children('.d444').val();
            }
            this.id=a;
            this.odds=b;
            this.result=c;
            var obj={
                cid:this.id,
                tz_odds:this.odds,
                tz_result:this.result,
                tz_money:val
            }
            if(this.arr.length==0){
                this.arr.push(obj);
            }else{
                var a=true;
                this.arr.forEach((e,i)=>{
                    if(e.cid==obj.cid&&e.tz_result==obj.tz_result){
                        a=false;
                        e.tz_money=obj.tz_money;
                    }else{
                        a=true;
                    }
                    if(e.tz_money==""){
                        this.cids=e.cid;
                        this.results=e.tz_result;
                        this.arr.forEach((e,i)=>{
                            if(e.cid==this.cids&&e.tz_result==this.results){
                                this.arr.splice(i,1); 
                            }
                        })
                    }
                })
                if(a==true){
                    this.arr.push(obj);
                }
            }
        },
        countdown(){
            this.http.post("/lol/lol/index")
            .then(res=>{  
                if(res.data.code==1){
                    this.list=res.data.msg;
                    this.list.forEach((e,i)=>{
                        e.child.forEach((f,i)=>{
                            if(f.status==0){
                            this.count=setInterval(()=>{ 
                                f.countdown--;
                                if(e.countdown<=1){
                                    this.comList();
                                }
                            },1000)
                        }
                        })
                        
                    })
                }else{
                    layer.msg(res.data.msg,{time:1500});
                }
           });
          
     
        },
        touz(){
            this.arr.forEach((e,i)=>{
                if(e.tz_money==""){
                    this.arr.splice(i,1);   
                }
            })
            var arrs = JSON.stringify(this.arr); 
            console.log(arrs);
            var touzArr={
                token:this.token,
                tz_arr:arrs
            }
            var qs = require('querystring');
            this.http.post("/lol/lol/add_order/",qs.stringify(touzArr))
            .then(res=>{
                if(res.data.code==1){
                    this.arrs=[];
                    this.arr=[];
                    $(".hh").find('.u').find('.d3').children('.dd').val("");
                    $(".hh").find('.u').find('.d4').children('.d444').val("");
                    $(".hh").find('.ss').children('.si').val("");

                    layer.msg(res.data.msg,{time:3500});
                    this.comList();
                    this.balanceList();
                    this.tokenList();   
                }else{
                    this.arrs=[];
                    this.arr=[];
                    $(".hh").find('.u').find('.d3').children('.dd').val("");
                    $(".hh").find('.u').find('.d4').children('.d444').val("");
                    $(".hh").find('.ss').children('.si').val("");
                    layer.msg(res.data.msg,{time:3500});
                    this.tokenList();

                }
            })
        },
        ruleClick(){
            this.show=true;
        },
        xxClick() {
            this.show = false;
        },

   },
    components:{
 
    },
    filters:{
        sz(s){
            if(s<10){
                return s='0'+s;
            }else{
                return s;
            }
        },
        bb(s){
            if(s==3){
                return s='BO3';
            }else if(s==5){
                return s='BO5';
            }
        }
    }
}



