export default {
    data:function(){
        return{
            share:[],
            syshow:true,
            xjshow:false,
            xjyh:[]
        }
    },
    methods:{
        shareList(){
            this.http.post("/home/user/share/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.share=res.data.msg;
                }
            })
        },
        xjyhList(){
            this.http.post("/home/user/child_yh/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.xjyh=res.data.msg;
                }
            })
        },
        isloginList(){
                this.http.post("/home/login/is_login/")
                .then(res=>{
                
                if(res.data.code==0){

                        window.location.href="#/"
                    }else if(res.data.code==1){
 
                        window.location.href="#/share"
                    }
            })
        },
        backClick(){
            window.location.href="#/mine";

        },
        syClick(){
            $("#i1 p").addClass("red");
            $("#i2 p").removeClass("red");
            $("#i2 p").addClass("grey");

            this.syshow=true;
            this.xjshow=false;
        },
        xjClick(){
            $("#i2 p").addClass("red");
            $("#i1 p").addClass("grey");

            $("#i1 p").removeClass("red");
            this.xjshow=true;
            this.syshow=false;
        },
        ys(){
            $("#i1 p").addClass("red");
            $("#i2 p").addClass("grey");
        }

    },
    mounted(){
        this.shareList();
        this.isloginList();
        this.ys();
        this.xjyhList();
    },
    filters:{
        jia(j){
                j='+'+j;
                return j;

        },
        fi(s){
            s=s.substring(0,s.length-3);
            return s;

        }
    }
}



