export default {
  data:function(){
    return{
       rev:"",
       qrev:""
    }
  },
  methods:{
    revBlur(){
        var reg = /^.{6,}$/;
        if(!$("#rev").val().trim()){
            layer.tips("","#rev",{tips:1});
            return;
        }
        if(!reg.test($("#rev").val())){
            layer.tips("","#rev",{tips:1});
            return;
        }
    },
    revs(){
     
        if(!$("#qrev").val().trim()){
            layer.tips("","#qrev",{tips:1});
            return;
        }
        if($("#rev").val().trim()!=$("#qrev").val().trim()){
    
            layer.tips("","#qrev",{tips:1});
            return;  
        }
       
    },
    revClick(){
      var reg = /^.{6,}$/
      if(!reg.test($("#rev").val())||!$("#qrev").val().trim()||$("#rev").val().trim()!=$("#qrev").val().trim()){
        layer.msg("",{time:1500})
      }else{
          var revArr={
          password:this.rev,
          zpassword:this.qrev
        }
        var qs = require('querystring');
        this.http.post("/home/login/save_mm/",qs.stringify(revArr))
        .then(res=>{
          if(res.data.code==0){
              layer.msg(res.data.msg,{time:1500});
          }else if(res.data.code==1){
            layer.msg(res.data.msg,{time:1500});
              location.href="#/";
              this.rev="";
              this.qrev="";
          }

        })
      }
     
    },
    backClick(){
          window.location.href='#/tsecurity';
      },
  }
}



