import {mapState} from 'vuex';
export default {
     data:function(){
        return{
          phone:''
        }
      },

      methods:{
          mbClick(){
          
              var mbArr={
                  phone:this.phone,
        
              }
              var qs = require('querystring');
              this.http.post('/home/login/forget_mm/',qs.stringify(mbArr))
              .then(res=>{
             
                  if(res.data.code==0){
                      layer.msg(res.data.msg,{time:1500});
                    
                  }else if(res.data.code==1){
                      layer.msg("",{time:1500});
                      window.location.href="#/tsecurity"
                      
                  }
              })
          },
          backClick(){
              window.location.href='#/';
          },
      }
}



