            })
            var arrs = JSON.stringify(this.arr); 
            var tou={
                tz_result:arrs,
                token:this.token
            }
            var qs = require('querystring');

            this.http.post("/lol/champion/add_order",qs.stringify(tou))
            .then(res=>{
                if(res.data.code==1){
                    layer.msg(res.data.msg,{time:1500});
                    this.catheshow=false;
                    this.tokenList();
                    this.balanceList();
                    this.arr=[];
