import {mapState} from 'vuex';
export default {
    data:function(){
        return{
            mbanswer1:'',
            mbanswer2:'',
    
        }
    },
    computed:{
        ...mapState(['tmb']),
    },
    mounted:function(){
        this.$store.dispatch("tmbList");
    },
    methods:{
          mbClick(){
              var mbArr={
           
                  mbanswer1:this.mbanswer1,
                  mbanswer2:this.mbanswer2
              }
              var qs = require('querystring');
              this.http.post('/home/login/decide_mb/',qs.stringify(mbArr))
              .then(res=>{
                  if(res.data.code==0){
                      layer.msg(res.data.msg,{time:1500});
                  }else if(res.data.code==1){
                      layer.msg("",{time:1500});
                      window.location.href="#/pass";
                      this.mbanswer1='';
                      this.mbanswer2='';
                  }
              })
          },
           backClick(){
              window.location.href='#/phone';
          },
    }
}



