export default {
    data:function(){
        return{
            invite:[]
        }
    },
    methods:{
        backClick(){
            window.location.href="#/bacinvite";
        },
        inviteList(){
            this.http.post("/home/user/invite_friend/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.invite=res.data.msg;
                }
            })
        },
        isloginList(){
            this.http.post("/home/login/is_login/")
            .then(res=>{
                if(res.data.code==0){
                    window.location.href="#/"
                }else if(res.data.code==1){

                    window.location.href="#/bacinvites"
                }
            })
        }
    },
    mounted(){
        this.inviteList();
        this.isloginList();
    }
}



