     import Login from '../components/Login.vue'
     export default {
         data:function(){
            return{
                order:[],
                login:''
            }
        },
        mounted(){
            this.render();
        },
        methods:{
            render(){
                this.http.post("/football/football/order/")
                .then(res=>{
                    if(res.data.code==0){
    
                    }else if(res.data.code==1){
                        this.order=res.data.msg;
                    }
                
                })
                
            },
            isloginList(){
                        this.http.post("/home/login/is_login/")
                        .then(res=>{
                        
                        if(res.data.code==0){
                                window.location.href="#/";
                            }else if(res.data.code==1){
                                window.location.href="#/footorder";
                            }
                    })
                },
                backClick(){
                    //history.back();
                    window.location.href="#/footmine";

                },
                btnClick(i){
                    this.$router.push({path:'/footdetail',query:{id:this.order[i].order_id}});
                },
        }
}



