export default {
    data: function () {
        return {
            show: false,
            balance:'',
            mon:'',
            cont:[],
            animals: [
                {
                name: '', num: '11.33',n:'30001'
            },
             {
                name: '', num: '11.33',n:'30002'
            }, 
            {
                name: '', num: '11.33',n:'30003'
            },
             {
                name: '', num: '11.33',n:'30004'
            },
             {
                name: '', num: '11.33',n:'30005'
            },
             {
                name: '', num: '11.33',n:'30006'
            }, 
            {
                name: '', num: '11.33',n:'30007'
            }, 
            {
                name: '', num: '11.33',n:'30008'
            }, 
            {
                name: '', num: '11.33',n:'30009'
            }, 
            {
                name: '', num: '11.33',n:'30010'
            }, 
            {
                name: '', num: '11.33',n:'30011'
            },
             {
                name: '', num: '11.33',n:'30012'
            },],
            sixelement: [{
                name: '', num: '5.80',n:'20001'
            }, 
            {
                name: '', num: '5.80',n:'20002'
            }, 
            {
                name: '', num: '5.80',n:'20003'
            },
             {
                name: '', num: '5.80',n:'20004'
            }, 
            {
                name: '', num: '5.80',n:'20005'
            },
             {
                name: '', num: '5.80',n:'20006'
            },],
            qiankun: [{
                name: '', num: '1.88',n:'10001'
            },
            {
                name: '', num: '1.88',n:'10002'
            },],
            token:'',
            kjid:'',
            sj:'',
            kj:'',
            seconds:'',
            count:'',
            timer:''
           
        }
      
    },
    mounted() {
        this.balanceList();
        this.tokenList();
        this.kjd();
        this.isloginList();
        this.djs();
    },
    methods: {
        cz(){
            window.location.href='#/dialmine';
        },
        kjd(){
            this.http.post("/dial/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.second=res.data.msg.Kjdjs;
                    // this.kjid=res.data.msg.kjid;
                    this.kjid=res.data.msg.kjid;

                    var now = Math.floor((new Date().getTime()/1000).toString());
                    this.sj=now+this.second; 
        
        
                }

            }) 

          
        },
        kjs(){
            var nows = Math.floor((new Date().getTime()/1000).toString());
            this.seconds=this.sj-nows;
            if(this.seconds<1){
                this.kjd();
            }
        },
          djs(){
            if (!this.timer) {                        
                this.timer = setInterval(() => {   
                    this.kjs();
                    this.seconds=Math.floor(this.seconds%60);
                    this.count=Math.floor(this.seconds/60);    
             
                }, 1000)     
            } 
        },
          isloginList(){
                this.http.post("/home/login/is_login/")
                .then(res=>{
                
                if(res.data.code==0){
                        window.location.href="#/";
                    }else if(res.data.code==1){
                      window.location.href="#/dialbet";
                    }
            })
        },
        rateClick() {
            this.show = true;
        },
        xxClick() {
            this.show = false;
        },
        backClick(){
            window.location.href='#/dial';
        },
        balanceList(){
            this.http.post("/dial/game/balance/")
            .then(res=>{
                if(res.data.code==0){       
                }else if(res.data.code==1){
                    this.balance=res.data.data;
                }
            })
        
        },
        tokenList(){
            this.http.post("/dial/game/token/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.token=res.data.token;
                }
            })
        },
        ksClick(k){
            if($("#i1").hasClass("red")){
                this.cont.forEach((e,i)=>{
                    if(JSON.stringify(e)==JSON.stringify(k)){
                       this.cont.splice(i,1);   
                        
                    }
                }) 
                $("#i1").removeClass("red");
                $("#i1").children("span").removeClass("res");
                $("#i1").children("p").removeClass("res");

                              
            }else{
               
                this.cont.push(k);
                $("#i1").addClass("red");
                $("#i1").children("span").addClass("res");
                $("#i1").children("p").addClass("res");

            }

        },
        ks1Click(k){
            if($("#i2").hasClass("red")){
                this.cont.forEach((e,i)=>{
                    if(JSON.stringify(e)==JSON.stringify(k)){
                       this.cont.splice(i,1);   

                    }
                }) 
                $("#i2").removeClass("red");
                $("#i2").children("span").removeClass("res");
                $("#i2").children("p").removeClass("res");

                              
            }else{
               
                this.cont.push(k);
                $("#i2").addClass("red");
                $("#i2").children("span").addClass("res");
                $("#i2").children("p").addClass("res");

            }

        },
        sxClick(i){
            var obj={
                n:this.animals[i].n
            }
            if($(".animals").eq(i).hasClass("ress")){
                this.cont.forEach((e,i)=>{
                    if(JSON.stringify(e)==JSON.stringify(obj.n)){
                        this.cont.splice(i,1);     
                                  
                    }
                })    
                $(".animals").eq(i).removeClass("ress");
                      
            }else{
                this.cont.push(obj.n);
                $(".animals").eq(i).addClass("ress");
            }
        },
        lhClick(i){
            var obj={
                n:this.sixelement[i].n
            }
            if($(".sixelement").eq(i).hasClass("ress")){
                this.cont.forEach((e,i)=>{
                    if(JSON.stringify(e)==JSON.stringify(obj.n)){
                        this.cont.splice(i,1);                     
                    }
                })    
                $(".sixelement").eq(i).removeClass("ress");
                      
            }else{
                this.cont.push(obj.n);
                $(".sixelement").eq(i).addClass("ress");
            }
        },
        qkClick(i){
            var obj={
                n:this.qiankun[i].n
            }
            if($(".qiankun").eq(i).hasClass("ress")){
                this.cont.forEach((e,i)=>{
                    if(JSON.stringify(e)==JSON.stringify(obj.n)){
                        this.cont.splice(i,1);                     
                    }
                })    
                $(".qiankun").eq(i).removeClass("ress");
                      
            }else{
                this.cont.push(obj.n);
                $(".qiankun").eq(i).addClass("ress");
            }
        },
        touClick(){
            var conts=this.cont.toString();

            var tou={
                tz_result:conts,
                order_money:this.mon*this.cont.length,
                token:this.token,
                kjid:this.kjid


            }
            var qs = require('querystring');

            this.http.post("/dial/game/touz/",qs.stringify(tou))
            .then(res=>{
                if(res.data.code==1){
                    layer.msg(res.data.msg,{time:1500});
                    this.tokenList();

                    this.cont=[];
                  
                    this.mon='';
                    $("#i1").removeClass("red");
                    $("#i1").children("span").removeClass("res");
                    $("#i1").children("p").removeClass("res");                      
                    $("#i2").removeClass("red");
                    $("#i2").children("span").removeClass("res");
                    $("#i2").children("p").removeClass("res");
                    $(".animals").removeClass("ress");
                    $(".sixelement").removeClass("ress");
                    $(".qiankun").removeClass("ress");
                }else{
                    layer.msg(res.data.msg,{time:1500});
                    this.tokenList();

                }
            })
        },

    },
    computed:{
          
          isYe(){
              return  this.balance.ye*1>=this.mon*this.cont.length;
         },
       
      },
      filters:{
        seco(s){
            if(s<10){
                s='0'+s;
            }
            return s;

        }
    },
}



