                                this.jg=res.data.msg.list[0].Kjjg;
                                this.kz=this.jg.substring(0,1);
                                this.ky=this.jg.substring(this.jg.length,1);  
                                this.dd=(res.data.msg.odds).slice(0,2);
                                this.dd1=(res.data.msg.odds).slice(2,4);
                                this.dd2=(res.data.msg.odds).slice(4,10);
                                this.d3=(res.data.msg.odds).slice(10,16);
