                                    this.kz=this.jg.substring(0,1);
                                    this.ky=this.jg.substring(this.jg.length,1);                      
                                }

                            }) 
                            this.tokenList();
                        }
                        if(this.seconds<=2){
                            this.index1=this.kz-1; 
                            this.index2=this.ky-1; 
                        }
                        if(this.seconds==60||this.seconds==59||this.seconds==58||this.secodns==57){
                            this.dices=false;
                 
                            this.d1=false;
                            this.d2=true;
                            this.index1=this.kz-1; 

                            this.index2=this.ky-1; 
                            this.balanceList(); 


                        }
                        
                        if(this.seconds>10||this.seconds<-1){
                            this.dices=false;
                            this.wen=true;
                            this.d1=false;
                            this.d2=false;  
                            this.index1=this.kz-1; 
                            this.index2=this.ky-1; 
                        }   
                               
                       

                    },1000)    
                   
                   
