        filters:{
            seco(s){
                if(s<10){
                    s='0'+s;
                }
                return s;

            },
            z(h){
                h = h.substring(0,1);
                return h;
            },
            y(b){

               b = b.substring(b.length,1);
                return b;
            },
            yz(b){
                b = b.substr(b.length-1,1);
                return b;
            }

        },
     
        computed:{
            
            isYe(){
                return  this.balance.ye*1>=this.mon*this.cont.length;
            },
