

import Swiper from 'vux/src/components/swiper/swiper.vue'


export default {
data:function(){
return{
list:[
{
url: 'football',
img: require('../assets/images/zq1.png'),
},
{
url: 'nba',
img: require('../assets/images/nba1.png'),
},
{
url: 'baccarat',
img: require('../assets/images/jjl.png'),
},








{
url: 'invit',
img: require('../assets/images/yao.png'),
},

],
id:'',
show:false

}
},
methods:{
backClick(){
window.location.href='#/';
},
alertClick(){
if(this.id=='true'){
this.show=true;
}
},
xClick(){
this.show=false;
},
aClick(){
window.location.href='#/invit';
}
},
mounted(){
if(this.$route.query.id){
this.id=this.$route.query.id;
}
this.alertClick();
},
components: {
Swiper
}

}



