export default {
    data: function () {
        return {
            show: false,
            catheshow:false,
            count:'',
            i1:'',
            i2:'',
            i3:'',
            dial:'',
            balance:'',
            token:'',
            kj:'',
            sj:'',
            seconds:'',
            second:'',
            nums:'',
            sound:'',
            timer:null,
            tss:null,
            animals: [
                {
                    name: '', num: '11.33',n:'30001'
                },
                {
                    name: '', num: '11.33',n:'30002'
                }, 
                {
                    name: '', num: '11.33',n:'30003'
                },
                {
                    name: '', num: '11.33',n:'30004'
                },
                {
                    name: '', num: '11.33',n:'30005'
                },
                {
                    name: '', num: '11.33',n:'30006'
                }, 
                {
                    name: '', num: '11.33',n:'30007'
                }, 
                {
                    name: '', num: '11.33',n:'30008'
                }, 
                {
                    name: '', num: '11.33',n:'30009'
                }, 
                {
                    name: '', num: '11.33',n:'30010'
                }, 
                {
                    name: '', num: '11.33',n:'30011'
                },
                {
                    name: '', num: '11.33',n:'30012'
                },
            ],
            sixelement: [
                {
                    name: '', num: '5.80',n:'20001'
                }, 
                {
                    name: '', num: '5.80',n:'20002'
                }, 
                {
                    name: '', num: '5.80',n:'20003'
                },
                {
                    name: '', num: '5.80',n:'20004'
                }, 
                {
                    name: '', num: '5.80',n:'20005'
                },
                {
                    name: '', num: '5.80',n:'20006'
                },
            ],
            qiankun: [
                {
                    name: '', num: '1.88',n:'10001'
                },
                {
                    name: '', num: '1.88',n:'10002'
                },
            ],
            teshu:[
                {
                    name:'--',num:'144',n:'40001'
                },
                {
                    name:'-',num:'72',n:'40002'
                },
            ],
            id:'',
            arr:[],
            ids:'',
            token:'',
            kjid:''


        }
    },
 
    beforeDestroy () {
        clearInterval(this.tss);
       
        clearInterval(this.timer);
    },
    methods: {
        tokenList(){
            this.http.post("/dial/game/token/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.token=res.data.token;
                }
            })
        },
        isloginList(){
            this.http.post("/home/login/is_login/")
            .then(res=>{
            
            if(res.data.code==0){

                    window.location.href="#/"
                }else if(res.data.code==1){

                    window.location.href="#/dial"
                }
        })
        },
        backClick(){
                    window.location.href='#/choose';
                },
        rateClick() {
            this.show = true;
            this.historyList();
        },
        xxClick() {
            this.show = false;
        },
        cancelClick() {
            this.catheshow = false;
        },
        buttClick() {
            console.log(11);
            var arrs = JSON.stringify(this.arr); 

            var tou={
                tz_result:arrs,
                token:this.token,
                kjid:this.kjid
            }
            var qs = require('querystring');
            this.http.post("/dial/game/touz/",qs.stringify(tou))
            .then(res=>{
                if(res.data.code==1){
                    layer.msg(res.data.msg,{time:1500});
                    this.tokenList();
                    this.arr=[];
                    $(".qiankun").find('input').val("");
                    $(".sixelement").find('input').val("");
                    $(".animal").find('input').val("");
                    $(".special").find('input').val(""); 
                    this.balanceList();
                }else{
                    this.arr=[];
                    $(".qiankun").find('input').val("");
                    $(".sixelement").find('input').val("");
                    $(".animal").find('input').val("");
                    $(".special").find('input').val(""); 
                    layer.msg(res.data.msg,{time:1500});
                    this.tokenList();


                }
            })            
        },
        kjd(){
            this.http.post("/dial/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.second=res.data.msg.Kjdjs;
                    this.kjid=res.data.msg.kjid;
                    var now = Math.floor((new Date().getTime()/1000).toString());
                    this.sj=now+this.second; 
                }

            })  
        },
        kjs(){
            var nows = Math.floor((new Date().getTime()/1000).toString());
            this.seconds=this.sj-nows;
            if(this.seconds<1){
                this.kjd();
            }
        },
        xhList(){
            this.tss=setInterval(()=>{

                if(this.seconds==0){                      
                    var audio = document.getElementById('audio');                  
                    audio.play();                        
                
                }else{
                    var audio = document.getElementById('audio');                
                    audio.pause();
                }
            },1000)
        },

          djs(){
            if (!this.timer) {                        
                this.timer = setInterval(() => {   
                    this.kjs();
                    this.seconds=Math.floor(this.seconds%60);
                    this.count=Math.floor(this.seconds/60);    
                if(this.seconds<=10&&this.seconds>=0){
                    $("#roule3").addClass('ani2');
                    $("#roule2").addClass('ani1');
                    $("#roule1").addClass('ani');
                }
                if(this.seconds==60||this.seconds==59||this.seconds==58||this.seconds==57){
                    this.balanceList();

                }                   

             
                 if(this.seconds==3){
                    var tou={
                        kjid:this.kjid
                    }
                    var qs = require('querystring');
                    this.http.post("/dial/game/get_result/",qs.stringify(tou))
                    .then(res=>{
                        if(res.data.code==0){
                            
                        }else if(res.data.code==1){

                            // this.dial=res.data.msg;
                            this.numm=res.data.msg.Kjjg;
                           
                      
                           
                            }

                        }) 

                }else if(this.seconds==2){
                    this.i1=this.numm.substring(0,5);
                }else if(this.seconds==1){
                    this.i2=this.numm.substring(6,11);
                }else if(this.seconds==0){
                    this.i3=this.numm.substring(12,17);
                }
                }, 1000)  
            }
        },
        historyList(){
            this.http.post("/dial/game/history/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.dial=res.data.msg;
                    this.nums=res.data.msg[0].Kjjg;
                    this.i1=this.nums.substring(0,5);
                    this.i2=this.nums.substring(6,11);
                    this.i3=this.nums.substring(12,17);
                    

        
                }

            }) 
        },
        balanceList(){
            this.http.post("/dial/game/balance/")
            .then(res=>{
                if(res.data.code==0){       
                }else if(res.data.code==1){
                    this.balance=res.data.data;
                }
            })
        
        },
        qiankunClick(a,i){
            this.id=a;
            var val = $(".qiankun").eq(i).find('input').val();
            var obj={
                id:this.id,
                tz_money:val
            }
            if(this.arr.length==0){
                this.arr.push(obj);
            }else{
                var a=true;
                this.arr.forEach((e,i)=>{
                    if(e.id==obj.id){
                        a=false;
                        e.tz_money=obj.tz_money;
                    }else{
                        a=true;
                    }
                    if(e.tz_money==""){
                        this.ids=e.id;
                        this.arr.forEach((e,i)=>{
                            if(e.id==this.ids){
                                this.arr.splice(i,1); 
                            }
                        })
                    }
                })
                if(a==true){
                    this.arr.push(obj);
                }
            }

        },
        lhClick(a,i){
            this.id=a;
            var val = $(".sixelement").eq(i).find('input').val();
            var obj={
                id:this.id,
                tz_money:val
            }
            if(this.arr.length==0){
                this.arr.push(obj);
            }else{
                var a=true;
                this.arr.forEach((e,i)=>{
                    if(e.id==obj.id){
                        a=false;
                        e.tz_money=obj.tz_money;
                    }else{
                        a=true;
                    }
                    if(e.tz_money==""){
                        this.ids=e.id;
                        this.arr.forEach((e,i)=>{
                            if(e.id==this.ids){
                                this.arr.splice(i,1); 
                            }
                        })
                    }
                })
                if(a==true){
                    this.arr.push(obj);
                }
            }
        },
        sxClick(a,i){
            this.id=a;
            var val = $(".animal").eq(i).find('input').val();
            var obj={
                id:this.id,
                tz_money:val
            }
            if(this.arr.length==0){
                this.arr.push(obj);
            }else{
                var a=true;
                this.arr.forEach((e,i)=>{
                    if(e.id==obj.id){
                        a=false;
                        e.tz_money=obj.tz_money;
                    }else{
                        a=true;
                    }
                    if(e.tz_money==""){
                        this.ids=e.id;
                        this.arr.forEach((e,i)=>{
                            if(e.id==this.ids){
                                this.arr.splice(i,1); 
                            }
                        })
                    }
                })
                if(a==true){
                    this.arr.push(obj);
                }
            }
        },
        ksClick(a,i){
            this.id=a;
            var val = $(".special").eq(i).find('input').val();

            var obj={
                id:this.id,
                tz_money:val
            }
            if(this.arr.length==0){
                this.arr.push(obj);
            }else{
                var a=true;
                this.arr.forEach((e,i)=>{
                    if(e.id==obj.id){
                        a=false;
                        e.tz_money=obj.tz_money;
                    }else{
                        a=true;
                    }
                    if(e.tz_money==""){
                        this.ids=e.id;
                        this.arr.forEach((e,i)=>{
                            if(e.id==this.ids){
                                this.arr.splice(i,1); 
                            }
                        })
                    }
                })
                if(a==true){
                    this.arr.push(obj);
                }
            }
        },
        scrolls(){
            document.documentElement.scrollTop=0;
        }
     
    },
    mounted(){
        clearInterval(this.timer);

        this.djs();
        this.historyList();
        this.balanceList();
        this.kjd();
        this.isloginList();
        this.xhList();
        this.tokenList();
        this.scrolls();
    },
    filters:{

        kj1(s){
            s = s.substring(0,5);
            if(s=='10001'){
                s='';
            }else if(s=='10002'){
                s='';
            }
            return s;
        },
        kj2(h){
            h = h.substring(6,11);
           if(h=='20001'){
                h='';
            }else if(h=='20002'){
                h='';
            }else if(h=='20003'){
                h='';
            }else if(h=='20004'){
                h='';
            }else if(h=='20005'){
                h='';
            }else if(h=='20006'){
                h='';
            }
            return h;
        },
        kj3(k){
            k = k.substring(12,17);
           if(k=='30001'){
                k='';
            }else if(k=='30002'){
                k='';
            }else if(k=='30003'){
                k='';
            }else if(k=='30004'){
                k='';
            }else if(k=='30005'){
                k='';
            }else if(k=='30006'){
                k='';
            }else if(k=='30007'){
                k='';
            }else if(k=='30008'){
                k='';
            }else if(k=='30009'){
                k='';
            }else if(k=='30010'){
                k='';
            }else if(k=='30011'){
                k='';
            }else if(k=='30012'){
                k='';
            }
            return k;
        },
        Kj4(s){
            if(s=='40001'){
                return  s='';
            }else if(s=='40002'){
                return s='';
            }
        }
    }

}




