export default {
    data:function(){
        return{
            id:'',
            detail:[]
        }
    },
    methods:{
   
         isloginList(){
            this.http.post("/home/login/is_login/")
            .then(res=>{  
                if(res.data.code==0){
                    window.location.href="#/"
                }else if(res.data.code==1){

                    this.$router.push({path:'footdetail',query:{id:this.id}});
                }
            })
        },
        backClick(){
            window.location.href="#/footorder";
        },
        detailList(){
            var obj={
                order_id:this.id
            }
            var qs = require('querystring');
            this.http.post("/football/football/order_info/",qs.stringify(obj))
            .then(res=>{
                if(res.data.code==1){
                    this.detail=res.data.msg;
                }else{

                }
            })
        }
    },
    mounted(){
        if(this.$route.query.id){
            this.id=this.$route.query.id;
        }
        this.isloginList();
        this.detailList();
    }
}



