import {mapState} from 'vuex';
import Login from '../components/Login.vue'

export default {
    data:function(){
        return{
            order:[],
            login:''
        }
    },
    methods:{
        render(){
            this.http.post("/lol/champion/order/")
           .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.order=res.data.msg;
                }
             
           })
           
        },
        isloginList(){
                    this.http.post("/home/login/is_login/")
                    .then(res=>{
                    
                    if(res.data.code==0){
                            window.location.href="#/";
                        }else if(res.data.code==1){
                            window.location.href="#/guessorder";
                        }
                })
            },
             backClick(){
                //history.back();
                 window.location.href="#/guessmine";

            },
           
    },

  mounted:function(){
      this.render();
      this.isloginList();
  },
  components:{
      Login
  },
  filters:{
      sz(s){
          if(s=='1'){
              return s='RNG';
          }else if(s=='2'){
              return s='EDG';
          }else if(s=='3'){
              return s='IG';
          }else if(s=='4'){
              return s='KT';
          }else if(s=='5'){
              return s='Gen.G';
          }else if(s=='6'){
              return s='AFS';
          }else if(s=='7'){
              return s='C9';
          }else if(s=='8'){
              return s='FNC';
          }else if(s=='9'){
              return s='G2';
          }else if(s=='10'){
              return s='VIT';
          }else if(s=='11'){
              return s='FW';
          }else if(s=='12'){
              return s='100T';
          }else if(s=='13'){
              return s='TL';
          }else if(s=='14'){
              return s='MAD';
          }else if(s=='15'){
              return s='PVB';
          }else if(s=='16'){
              return s='GRX';
          }else if(s=='17'){
              return s='GMB';
          }else if(s=='18'){
              return s='KLG';
          }else if(s=='19'){
              return s='KaBuM';
          }else if(s=='20'){
              return s='DFM';
          }else if(s=='21'){
              return s='SUP';
          }else if(s=='22'){
              return s='ASC';
          }else if(s=='23'){
              return s='INF';
          }else if(s=='24'){
              return s='DW';
          }
      }
      
  }
  
}



