            i7:'',
            i8:null,
            shue:{},
            kjdjs:''
        }
    },
    beforeDestroy () {
        clearInterval(this.timers);
    },
    methods:{
        backClick(){
            window.location.href="#/choose";
        },
        isloginList(){
            this.http.post("/home/login/is_login/")
            .then(res=>{
                if(res.data.code==0){
                    window.location.href="#/";
                }else if(res.data.code==1){
                    window.location.href="#/baccarat";
                }
            })
        },
        xxClick(){
            this.show=false;
            this.shows=false;
        },
        ruleClick(){
            this.show=true;
        },
        xzClick(a,i){
            this.money=a;
        },
        x1Click(a){
            this.xia=a;
            if(this.xia==''){
                this.xia='1';
            }else if(this.xia==''){
                this.xia='2';
            }else if(this.xia==''){
                this.xia='3';
            }else if(this.xia==''){
                this.xia='4';
            }else if(this.xia==''){
                this.xia='5';
            }
            var arr={
                tz_result:this.xia,
                tz_money:this.money,
                kjid:this.kjid,
                token:this.token
            }
            var qs = require('querystring');
            this.http.post("/bjl/game/touz/",qs.stringify(arr))
            .then(res=>{
                if(res.data.code==1){
                    layer.msg(res.data.msg,{time:2000});
                    this.tokenList();
                    this.balanceList();
                    this.xia='';
                    this.shueList();
                }else{
                    layer.msg(res.data.msg,{time:2000});
                    this.tokenList();
                    this.xia='';

                }
            })            
        },
        x2Click(a){
            this.xia=a;
            if(this.xia==''){
                this.xia='1';
            }else if(this.xia==''){
                this.xia='2';
            }else if(this.xia==''){
                this.xia='3';
            }else if(this.xia==''){
                this.xia='4';
            }else if(this.xia==''){
                this.xia='5';
            }
            var arr={
                tz_result:this.xia,
                tz_money:this.money,
                kjid:this.kjid,
                token:this.token
            }
            var qs = require('querystring');
            this.http.post("/bjl/game/touz/",qs.stringify(arr))
            .then(res=>{
                if(res.data.code==1){
                    layer.msg(res.data.msg,{time:2000});
                    this.tokenList();
                    this.balanceList();
                    this.xia='';
                    this.shueList();
                }else{
                    layer.msg(res.data.msg,{time:2000});
                    this.tokenList();
                    this.xia='';

                }
            })
           
        },
        historyClick(){
            this.shows=true;
        },
        kjd(){
            this.http.post("/bjl/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.second=res.data.msg.Kjdjs;
                    this.kjid=res.data.msg.kjid; 
                    var now = Math.floor((new Date().getTime()/1000).toString());
                    this.sj=now+this.second;
                  
                }

            })
        },
        kjs(){
            var nows = Math.floor((new Date().getTime()/1000).toString());
            this.seconds=this.sj-nows;
            if(this.seconds<1){
                this.kjd();
                this.balanceList();
                this.historyList();
                this.shueList();
            }
           
        },
        bacList(){
            this.timers=setInterval(()=>{
                this.kjs();
                this.jg();  
            },1000)    
        },
        tokenList(){
            this.http.post("/bjl/game/token/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.token=res.data.token;
                }
            })
        },
        balanceList(){
            this.http.post("/bjl/game/balance/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.balance=res.data.data;
                }
            })
            
        },
        historyList(){
            this.http.post("/bjl/game/history/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.history=res.data.msg;
                }
            })
       },
       cardList(){
        this.http.post("/bjl/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.kjid=res.data.msg.kjid; 
                    this.kjdjs=res.data.msg.Kjdjs; 
                    if(this.kjdjs<=8){
                        var arr={
                            kjid:this.kjid,
                        }
                        var qs = require('querystring');
                        this.http.post("/bjl/game/card/",qs.stringify(arr))
                        .then(res=>{
                            if(res.data.code==0){
                                
                            }else if(res.data.code==1){
                        
                                this.i5= res.data.msg.banker_left;
                                this.i6= res.data.msg.banker_right;
                                this.i3= res.data.msg.banker_three;
                                this.i4= res.data.msg.banker_result;
                                this.i1= res.data.msg.player_left;
                                this.i2= res.data.msg.player_right;
                                this.i7= res.data.msg.player_three;
                                this.i8= res.data.msg.player_result;   
                            }
                        })
                    }
                }
            }) 
        },
        oldList(){
            this.http.post("/bjl/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.kjdjs=res.data.msg.Kjdjs; 
                    if(this.kjdjs>20&&this.kjdjs<60){
                        this.http.post("/bjl/game/old_card/")
                        .then(res=>{
                            if(res.data.code==0){
                                
                            }else if(res.data.code==1){     
                                this.i5= res.data.msg.banker_left;
                                this.i6= res.data.msg.banker_right;
                                this.i3= res.data.msg.banker_three;
                                this.i4= res.data.msg.banker_result;
                                this.i1= res.data.msg.player_left;
                                this.i2= res.data.msg.player_right;
                                this.i7= res.data.msg.player_three;
                                this.i8= res.data.msg.player_result;
                            }
                        })
                    }   
                }
            })
        },
        jg(){
            if(this.seconds==7){
               this.i1=null;
               this.i2=null;
               this.i3=null;
               this.i4=null;
               this.i5=null;
               this.i6=null;
               this.i7=null;
               this.i8=null;
               this.http.post("/bjl/game/kj/")
                .then(res=>{
                    if(res.data.code==0){ 
                    }else if(res.data.code==1){
                        this.kjid=res.data.msg.kjid; 
                        var arr={
                            kjid:this.kjid,
                        }
                        var qs = require('querystring');
                        this.http.post("/bjl/game/card/",qs.stringify(arr))
                        .then(res=>{
                            if(res.data.code==0){
                                
                            }else if(res.data.code==1){
                                this.card=res.data.msg;   
                            }
                        })
                    }
                })
            }
            if(this.seconds==6){
               this.i1= this.card.player_left;
            }else if(this.seconds==5){
                this.i5= this.card.banker_left;
            }else if(this.seconds==4){
                this.i2= this.card.player_right;
            }else if(this.seconds==3){
                this.i6= this.card.banker_right;
            }else if(this.seconds==2){
                this.i7= this.card.player_three;
            }else if(this.seconds==1){
                this.i3= this.card.banker_three;
                this.i8= this.card.player_result;
                this.i4= this.card.banker_result;
            }
            if(this.seconds>10&&this.seconds<20){
                this.i1=null;
                this.i2=null;
                this.i3=null;
                this.i4=null;
                this.i5=null;
                this.i6=null;
                this.i7=null;
                this.i8=null;
            }
       },
       shueList(){
            this.http.post("/bjl/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.kjid=res.data.msg.kjid; 
                    var arr={
                        kjid:this.kjid,
                    }
                    var qs = require('querystring');
                    this.http.post("/bjl/game/get_tz/",qs.stringify(arr))
                    .then(res=>{
                        if(res.data.code==0){
                            
                        }else if(res.data.code==1){
                            this.shue=res.data.msg;  
                        }
                    })
                }
            })
        },
        scrolls(){
            document.documentElement.scrollTop=0;
        }
    },
    mounted() {
        this.isloginList();
        this.cardList();
        clearInterval(this.timers);
        this.bacList();
        this.kjd();
        this.tokenList();
        this.balanceList();
        this.historyList();
        this.shueList();
        this.oldList();
        this.scrolls();
    },
}



