export default {
    data:function(){
        return{
            account:[],
            login:''
        }
    },
    methods:{
        backClick(){
            window.location.href="#/mine";
        },
        cancelClick:function(i){
            $(".canc").eq(i).remove();
            var txArr={
                Txqqid:this.account[i].Txqqid
            }
            var qs = require('querystring');
            this.http.post('/home/user/del_tixian/',qs.stringify(txArr))
            .then(res=>{
                if(res.data.code==0){
                layer.msg(res.data.msg,{time:1500});
                this.accounts();
                }else if(res.data.code==1){
                
                layer.msg("",{time:1500});
                this.accounts();
                
                }
            })

        },
        accounts(){
            this.http.post('/home/user/account_details/')
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.account=res.data.msg;
                
                }
            
            })
        },
        isloginList(){
                this.http.post("/home/login/is_login/")
                .then(res=>{
                
                if(res.data.code==0){
                        window.location.href="#/";
                    }else if(res.data.code==1){
                        window.location.href="#/account";
                    }
            })
        },
    },
    mounted() {
        this.accounts();
        this.isloginList();
    },
    filters:{
        fi(s){
            s=s.substring(0,s.length-3);
            return s;
        },
        jia(j){
            if(j==1){
                j='+';
                return j;
            }else if(j==2){
                j="-";
                return j;
            }
        },
        tixian(t){
            if(t==1){
                t="";
                return t;
            }else if(t==2){
                t="";
                return t;
            }else if(t==3){
                t="";
                return t;
            }else if(t==4){
                t="";
                return t;
            }
        },
    },
}



