export default {
    data:function(){
      return{
        lphone:null, 
        lpsw:null
      }
    },
    mounted(){
      this.isloginList();
    },
    methods:{
      loginClick(){
        var loginArr={
          phone:this.lphone,
          password:this.lpsw
        }
        var qs = require('querystring');
        this.http.post("/home/login/login/",qs.stringify(loginArr))
        .then(res=>{
          if(res.data.code==0){
            layer.msg(res.data.msg,{time:1500});
          }else if(res.data.code==1){
            layer.msg("",{time:1500});
            this.$router.push({path:'/choose',query:{id:'true'}});
            this.lphone="";
            this.lpsw="";
          }else if(res.data.code==2){
            layer.msg(res.data.msg,{time:1500});

            window.location.href="#/security";
          }
        })
      },
      isloginList(){
          this.http.post("/home/login/is_login/")
          .then(res=>{
          
          if(res.data.code==0){

                  window.location.href="#/";
              }else if(res.data.code==1){
                this.$router.push({path:'/choose',query:{id:'true'}});

                  // window.location.href="#/choose";
              }
        
      })
  },

    }
}



