import {mapState} from 'vuex';
import SIdentify from '../components/Identify';
export default {
  name: "codetest",
  components:{
    SIdentify
  },
  data:function(){
    return {
      show:true,
      timer:null,
      count:'',
      rphone:"",
      rcode:"",
      rpsw:"",
      qrpsw:"",
      invite:"",
      yqm:'',
      identifyCodes: "1234567890",
      identifyCode: ""
    }
  },
  mounted(){
    this.yqmList();
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
    

  },
  methods:{
   
    yqmList(){
      if(window.location.hash=='#/regist'){

      }else{
        var y=window.location.hash;
      
        this.yqm=y.substr(y.length-6);
        
      if(this.yqm!=''){
       var yqmArr={
         yqm:this.yqm
       }
        var qs = require('querystring');
          this.http.post("/home/login/dianji/",qs.stringify(this.yqmArr))
          .then(res=>{
            if(res.data.code==1){
            
              layer.msg("",{time:1500});

            }else{
            }
          })
      }
      }
    

  
    },
    registClick(){
      if($("#rcode").val()==this.identifyCode){
          var registArr={
            phone:this.rphone,
            password:this.rpsw,
            zpassword:this.qrpsw,
            yqm:this.yqm,
          }
          var qs = require('querystring');
          this.http.post("/home/login/reg/",qs.stringify(registArr))
          .then(res=>{
            if(res.data.code==1){
            
              layer.msg("",{time:1500});
              window.location.href="#/security";
              this.rphone=null;
              this.rcode=null;
              this.rpsw=null;
              this.qrpsw=null;
              this.invite=null;
            

            }else{
              layer.msg(res.data.msg,{time:1500});
            }
          })

      }else{
        layer.msg('',{time:1500});
      }

    },
    phoneBlur(){
        var reg=/^[1][3,4,5,7,8][0-9]{9}$/;
        if(!reg.test($("#rphone").val())){
            layer.tips("","#rphone",{tips:1}); 
            return;  
               
        }
    },
    codeBlur(){
        if(!$("#rcode").val().trim()){
            layer.tips("","#rcode",{tips:1});
            return;
              
        }
    },
    pswBlur(){
        var reg = /^.{6,}$/;
        if(!$("#rpsw").val().trim()){
            layer.tips("","#rpsw",{tips:1});
            return;
        }
        if(!reg.test($("#rpsw").val())){
            layer.tips("","#rpsw",{tips:1});
            return;
        }
    },
    rpswBlur(){
      if(!$("#qrpsw").val().trim()){
          layer.tips("","#qrpsw",{tips:1});
          return;    
      }
      if($("#rpsw").val().trim()!=$("#qrpsw").val().trim()){
    
         layer.tips("","#qrpsw",{tips:1});

          return;  
      }
    },
    inviteBlur(){
        if(!$("#invite").val().trim()){
            layer.tips("","#invite",{tips:1});
            return;
              
        }
    },
    huanClick(){
        var ts = Date.parse(new Date())/1000;
        $('#codes').attr("src", "/home/login/getcode/?id="+ts);
          
    },
    backClick(){
        window.location.href='#/';
    },
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
    }

  },
 
}



