 import {mapState} from 'vuex';
 export default {
     data:function(){
         return{
             name:'',
             credit:''
         }
     },
     methods:{
         submitClick(){
             var attestArr={
                 username:this.name,
                 bank_no:this.credit
             }
             var qs = require('querystring');
             this.http.post("/home/user/set_renzheng/",qs.stringify(attestArr))
             .then(res=>{
                 if(res.data.code==0){
                     layer.msg(res.data.msg,{time:1500});
                 }else if(res.data.code==1){
                     layer.msg(res.data.msg,{timer:1500});
                     window.location.href="#/bacmine"
                     this.name="";
                     this.credit="";
                 }
             })
         },
         isloginList(){
             this.http.post("/home/login/is_login/")
             .then(res=>{
                 if(res.data.code==0){
                     window.location.href="#/"
                 }else if(res.data.code==1){
                     window.location.href="#/bacattest"
                 }
             })
         },
         backClick(){
             window.location.href="#/bacmine";
         },
         sfzBlur(){
             var reg=/^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
             if(!reg.test($("#number").val())){
                 layer.tips("","#number",{tips:1});
                 return;
             }
         },
         yhkBlur(){
             var reg=/^([1-9]{1})(\d{14}|\d{15}|\d{16}|\d{17}|\d{18})$/;
             if(!reg.test($("#credit").val())){
                 layer.tips("","#credit",{tips:1});
                 return;
             }
         },
         nameBlur(){
             if(!$("#name").val().trim()){
                 layer.tips("","#name",{tips:1});
                 return;
             }
         }
     },
     computed:{
         ...mapState(['attest']),

     },
     mounted(){
         this.isloginList();
     }

}



