export default {
    data: function () {
        return {
            show: false,
            tzshow:false,
            n:'',
            jine:'',
            sxshow:false,
            szshow:false,
            sztshow:false,
            wy:true,
            qx:false,
            qxshow:false,
            bz:false,
            shuju:[50,100,300,500,1000,3000,5000,10000],
            czshow:false,
            jgshow:false,
            count:'',
            counts:'',
            timer:null,
            timers:null,
            shangzhuang:false,
            i:'',
            i11:'',
            i12:'',
            i21:'',
            i22:'',
            i31:'',
            i32:'',
            i41:'',
            i42:'',
            con:'',
            tim:'',
            shu:'',
            seconds:'',
            sj:'',
            second:"",
            count:'',
            se:30,
            index:'',
            balance:'',
            che:null,
            zz:'',
            zy:'',
            sz:'',
            sy:'',
            xz:'',
            xy:'',
            tz:'',
            ty:'',
            chesslist:'',
            kjid:'',
            nu:'',
            lists:[], 
            kjids:'',
            banker:'',
            visit:'',
            nostril:'',
            surname:'',
            bankers:'',
            visits:'',
            nostrils:'',
            surnames:'',
            szsshow:false,
            szc:true,
            xzc:false,
            xzshow:false,
            tzz:false,
            zzz:false,
            time:10,
            times:null,
            zzzs:false,
            tii:30,
            jines:'',
            yes:'',
            people:'',
            pm:'',
            cou:'',
            zj:'',
            zzj:false,
            jx:true,
            z:'',
            jxx:true,
            szshows:false,
            szsshows:false,
            timesss:null,
            yess:''

        }
    },
          
    computed:{
                  
        isYe(){
            return  this.balance.ye*1>=this.jine;
        },
    },
    filters:{
        k1(s){
        s=s.substring(0,1);
        return s;
        },
        k2(s){
        s=s.substring(1,2);
        return s;
        },
        k3(s){
        s=s.substring(2,3);
        return s;
        },
    },
    mounted(){
        this.pai();
        clearInterval(this.timers);
        this.kj();  
        this.kjd();
        this.balanceList();
        this.xhList();
        this.isloginList();
        this.tokenList();
        this.historyList();
        this.yeList();
        this.pmList();
        this.zjList();
        this.scrolls();
    },
    beforeDestroy () {
        clearInterval(this.che);
        clearInterval(this.timers);
        clearInterval(this.ts);
        clearInterval(this.timesss);   
       

    },
    destroyed(){       
        clearInterval(this.che);
        clearInterval(this.timers);
        clearInterval(this.ts);
        clearInterval(this.timesss);         
    },
    created(){
        this.pai();

        this.kj();
    },
    methods: {
       
        scrolls(){
            document.documentElement.scrollTop=0;
        },
        pmList(){
            this.http.post("/nine/game/ranking/")
            .then(res=>{
                if(res.data.code==1){
                    this.pm=res.data.data.ranking;
                    this.cou=res.data.data.count;
                }else{
                
                }
            }) 
        },
        yeList(){
            this.http.post("/nine/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.kjid=res.data.msg.kjid;
                    var touzArr={
                        kjid:this.kjid,
                    }
                    var qs = require('querystring');
                    this.http.post("/nine/game/get_tz_money/",qs.stringify(touzArr))
                    .then(res=>{
                        if(res.data.code==1){
                            if(res.data.data.money>=0){
                                this.yes=res.data.data.money;
                                this.yess=res.data.data.banker_money;
                            }else{
                                this.yes=0;
                                this.yess=0;
                            }
                        }else{
                        
                        }
                    }) 
        
        
                }

            }) 
            this.timesss=setInterval(()=>{
                var y=this.yes;
                var rand=parseInt(Math.random()*(30-20+1)+20)/1000;
                var tz_money=this.yess*rand;
                this.yes=Math.round(y-tz_money);
                if(this.yes<=0){
                    this.yes=0;
                }
                if(this.seconds<=15&&this.seconds>=0){
                    clearInterval(this.timesss);
                    this.yes=0;
                }

                if(this.seconds==1){
                    this.http.post("/nine/game/kj/")
                    .then(res=>{
                        if(res.data.code==0){
                            
                        }else if(res.data.code==1){
                            this.kjid=res.data.msg.kjid;
                            var touzArr={
                                kjid:this.kjid,
                            }
                            var qs = require('querystring');
                            this.http.post("/nine/game/get_tz_money/",qs.stringify(touzArr))
                            .then(res=>{
                                if(res.data.code==1){
                                    if(res.data.data.money>=0){
                                        this.yes=res.data.data.money;
                                    }else{
                                        this.yes=0;
                                    }
                                }else{
                                
                                }
                            }) 
                
                
                        }

            }) 
                }
            },1000);
           
            
        },
        djs(){
             this.times=setInterval(()=>{
                this.time=this.seconds;
                
                if(this.time<=0){
                    this.time=0
                }else{
                    this.time--;

                }
                if(this.time==0){
                    clearInterval(this.times);
                    this.time=this.seconds;
                    this.zj=0;
                    this.zzj=false;

                }
                // if(this.time==50){
                //     this.jxx=true;
                // }
                // if(this.time==1){
                //     console.log(this.jxx);

                //     if(this.jxx==true){

                //         this.http.post("/nine/game/down_kamisho/")
                //         .then(res=>{
                //             if(res.data.code==1){
                //                 layer.msg("",{time:3000})
                //                 this.pmList();
                //                 this.jxx=true;


                //             }
                //         })
                //     }
                    
                // }
              
            },1000);
       
            
           
        },
        yd(){

            this.http.post("/nine/game/history/")
            .then(res=>{
            if(res.data.code==0){
                
            }else if(res.data.code==1){
                this.kjids=res.data.msg[0].kjid;
                var kjArr={
                    kjid:this.kjids
                }
                var qs = require('querystring');
                this.http.post("/home/user/inform/",qs.stringify(kjArr))
                .then(res=>{
                    if(res.data.code==0){

                    }else if(res.data.code==1){
                        this.lists=res.data.msg;
                        this.lists.forEach(function(e,i){
                            $(".z").eq(i).css({
                                position:'absolute',
                                left:(200+500*i)+"px" })
                            });
                            var cur=-(200+500*(this.lists.length-1));
                            var curs=this.lists.length;
                            $("#zh").css("width",(200+500*(this.lists.length-1)+"px"));
                            $("#zh").animate({
                                left:cur+'px', 
                            },curs*5000,"linear",function(){
                    
                        });
                        this.ts=setInterval(()=>{         
                            if($("#zh").css('left')==-(200+500*(this.lists.length-1))+'px'){
                                this.lists='';
                                $("#zh").css('left','200px');

                            }
                        },1000);

                    }
                })

            }

            }) 




            },
        touz(){
            if(this.jine>this.yes){
                this.tzz=true;
                this.tzshow=false;

            }else{
                var touzArr={
                    tz_result:this.nu,
                    order_money:this.jine,
                    kjid:this.kjid,
                    token:this.token
                }
                var qs = require('querystring');
                this.http.post("/nine/game/touz/",qs.stringify(touzArr))
                .then(res=>{
                    if(res.data.code==1){
                        this.tzshow=false;
                        this.balanceList();
                        this.tokenList();
                        this.arr=[];
                        this.mon='';
                    
                        this.jine="";
                        this.yeList();
                        layer.msg(res.data.msg,{time:3000});
                    }else{
                        this.tokenList();
                        layer.msg(res.data.msg,{time:1500});
                    }
                }) 
            }
           
            
                             
        },
        pai(){
            $("#tt1").addClass("non");
            this.http.post("/nine/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.seconds=res.data.msg.Kjdjs;
                    if(this.seconds>10){
                        this.http.post("/nine/game/old_card/")
                        .then(res=>{
                            if(res.data.code==1){

                                this.i=res.data.msg.deal_order;

                                this.i41=res.data.msg.card.banker_left;
                                this.i42=res.data.msg.card.banker_right;
                                this.i31=res.data.msg.card.visit_left;
                                this.i32=res.data.msg.card.visit_right;
                                this.i21=res.data.msg.card.nostril_left;
                                this.i22=res.data.msg.card.nostril_right;
                                this.i11=res.data.msg.card.surname_left;
                                this.i12=res.data.msg.card.surname_right;
                                this.banker=res.data.msg.num.banker_num;
                                this.visit=res.data.msg.num.visit_num;
                                this.nostril=res.data.msg.num.nostril_num;
                                this.surname=res.data.msg.num.surname_num;
                            }
                        })
                    
                    }else if(this.seconds<=10&&this.seconds>4){
                        var obj={
                            kjid:this.kjid
                        }
                        var qs = require('querystring');

                        this.http.post("/nine/game/deal_order/",qs.stringify(obj))
                        .then(res=>{
                            if(res.data.code==1){
                                this.i=res.data.msg.deal_order;
                            }
                        })
                    
                        this.i11='111';
                        this.i12='111';
                        this.i21='222';
                        this.i22='222';
                        this.i31='333';
                        this.i32='333';
                        this.i41='8443';
                        this.i42='8443';

                    }else if(this.seconds<=5&&this.seconds>=0){
                      
                        var obj={
                            kjid:this.kjid
                        }

                        var qs = require('querystring');

                        this.http.post("/nine/game/deal_order/",qs.stringify(obj))
                        .then(res=>{
                            if(res.data.code==1){
                                this.i=res.data.msg.deal_order;
                            }
                        })
                        this.http.post("/nine/game/card/",qs.stringify(obj))
                        .then(res=>{
                            if(res.data.code==1){
                                this.i41=res.data.msg.banker_left;
                                this.i42=res.data.msg.banker_right;
                                this.i31=res.data.msg.visit_left;
                                this.i32=res.data.msg.visit_right;
                                this.i21=res.data.msg.nostril_left;
                                this.i22=res.data.msg.nostril_right;
                                this.i11=res.data.msg.surname_left;
                                this.i12=res.data.msg.surname_right;
                            
                            }
                        })
                            

                       
                       
                    }
                }

            }) 
            
        },
      
        historyList(){
            this.http.post("/nine/game/history/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.chesslist=res.data.msg;
        
                }

            }) 
        },
        tokenList(){
            this.http.post("/nine/game/token/")
            .then(res=>{
                if(res.data.code==0){

                }else if(res.data.code==1){
                    this.token=res.data.token;
                }
            })
        },
        isloginList(){
                this.http.post("/home/login/is_login/")
                .then(res=>{
                
                if(res.data.code==0){
                        window.location.href="#/";
                    }else if(res.data.code==1){
                        window.location.href="#/chess";
                    }
             
            })
        },
        balanceList(){
            this.http.post("/nine/game/balance/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
               
                    this.balance=res.data.data.ye;
                   
                }
            })
         
        },
        xhList(){
            this.che=setInterval(()=>{
                if(this.seconds==1){
                    var audio = document.getElementById('audio');      
                    audio.play();
                
                }else{
                    var audio = document.getElementById('audio');       
                    audio.pause();
                }
            },1000)
        },
        kjd(){
            this.http.post("/nine/game/kj/")
            .then(res=>{
                if(res.data.code==0){
                    
                }else if(res.data.code==1){
                    this.second=res.data.msg.Kjdjs;
                    this.kjid=res.data.msg.kjid;
                    var now = Math.floor((new Date().getTime()/1000).toString());
                    this.sj=now+this.second;
        
        
                }

            }) 
        },
        zjList(){
            this.http.post("/nine/game/is_kamisho/")
            .then(res=>{
                if(res.data.code==1){
                    this.zj=res.data.data;
                    if(this.zj==1){
                        $("#tt1").removeClass("non");

                    }else{
                        $("#tt1").addClass("non");

                    }
                }
            })
            

        },
        kjs(){
            var nows = Math.floor((new Date().getTime()/1000).toString());
            this.seconds=this.sj-nows;
         
            if(this.seconds<10&&this.zj==0){
                $("#tt1").removeClass("non");

            }
            if(this.seconds<1){
                this.kjd();
                this.pmList();
                this.yeList();
                this.zjList();

            }
            if(this.seconds==10){
                // this.http.post("/nine/game/is_kamisho/")
                // .then(res=>{
                //     if(res.data.code==1){
                //         this.zj=res.data.data;

                //     }
                // })
              
                if(this.zj==1){
                    this.djs();
                    $("#tt1").addClass("non");
                    this.zzj=true;
                }else{

                    $("#tt1").addClass("non");
                    this.zzj=false;
                }

              
            }
            

        },
        kj(){
            this.timers=setInterval(()=>{
               
                this.kjs();
                this.seconds=Math.floor(this.seconds%60)+1;
                this.count=Math.floor(this.seconds/60);    
                this.sx();

            },1000)       
         
                    
        },
        sx(){
           if(this.seconds==11){
               this.i11='';
               this.i12='';
               this.i21='';
               this.i22='';
               this.i31='';
               this.i32='';
               this.i41='';
               this.i42='';
               this.banker='';
               this.visit='';
               this.nostril='';
               this.surname='';
           }
           if(this.seconds<=11&&this.seconds>=4){
                $("#c1").addClass("none");
                $("#c2").addClass("none");
                $("#c3").addClass("none");
                $("#c4").addClass("none");

           }
            // 10
            if(this.seconds<=11&&this.seconds>1){
                        var obj={
                            kjid:this.kjid
                        }
                        var qs = require('querystring');

                        this.http.post("/nine/game/deal_order/",qs.stringify(obj))
                        .then(res=>{
                            if(res.data.code==1){
                                this.i=res.data.msg.deal_order;
                            }
                        })
                    }
                    //1,5,9
                    if(this.seconds==9&&(this.i==1||this.i==2||this.i==3||this.i==4||this.i==17||this.i==18||this.i==19||this.i==20||this.i==33||this.i==34||this.i==35||this.i==36)){
                        this.i41=8443;
                        this.i42=8443;

                    }
                    if(this.seconds==8&&(this.i==1||this.i==2||this.i==3||this.i==4||this.i==17||this.i==18||this.i==19||this.i==20||this.i==33||this.i==34||this.i==35||this.i==36)){
                        this.i31=333;
                        this.i32=333;

                    }
                    if(this.seconds==7&&(this.i==1||this.i==2||this.i==3||this.i==4||this.i==17||this.i==18||this.i==19||this.i==20||this.i==33||this.i==34||this.i==35||this.i==36)){
                        this.i21=222;
