export default {
    data:function(){
      return{
        lphone:null, 
        lpsw:null
      }
    },
    methods:{
      loginClick(){
        console.log(this.lphone);
        var loginArr={
          phone:this.lphone,
          password:this.lpsw
        }
        var qs = require('querystring');
        this.http.post("/game/home/login/login/",qs.stringify(loginArr))
        .then(res=>{
          if(res.data.code==0){
            layer.msg(res.data.msg,{time:1500});
          }else if(res.data.code==1){
            layer.msg("",{time:1500});
            window.location.href="#/choose";
            this.lphone="";
            this.lpsw="";
          }else if(res.data.code==2){
            layer.msg(res.data.msg,{time:1500});

            window.location.href="#/security";
          }
        })
      },
    
    }
}



